
import { defineComponent, reactive, toRefs } from 'vue';
import { QrStream } from '../../../dist/vue3-qr-reader.common'

export default defineComponent({
  name: 'QrStreamExample',
  components: {
    QrStream
  },
  setup() {
    const state = reactive({
      data: null
    })
    function onDecode(data) {
      state.data = data
    }
    return {
      ...toRefs(state),
      onDecode
    }
  }
});
